@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@200;400;700&display=swap);
*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 100%;
}

body {
    font-family: "Outfit", sans-serif;
    background-color: #06121e;
    width: 100%;
    /* padding for the fixed header */
    /* padding-top: 4.063rem; */
}


/* body::-webkit-scrollbar {
    background-color: white;
    /* background-color: #06121e; */


/* width: 10px;

} */

body::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #06121e;
    border-radius: 10px;
}

body::-webkit-scrollbar {
    width: 3px;
    background-color: #f5f5f5;
}

body::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-image: -webkit-gradient( linear, left bottom, left top, color-stop(0.44, #1d7fe0), color-stop(0.72, #0d3c6b), color-stop(0.86, #06121e));
}

@media screen and (max-width: 415px) {
    html {
        /*13px*/
        font-size: 87.5%;
    }
}
.Header_header__3Cm3j {
    position: fixed;
    padding: 0.8rem 2rem;
    top: 0;
    left: 0;
    right: 0;
    /* background-color: #06121e; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 100;
    transition: 0.5s ease;
}

.Header_colored__2VbAS {
    background-color: #161853;
    transition: 0.5s ease;
}

.Header_logo__187aZ {
    cursor: pointer;
    height: 2rem;
    width: auto;
    object-fit: cover;
}

.Header_search__18FNT {
    font-size: 2rem;
    color: rgba(255, 255, 255, 0.671);
}

@media screen and (max-width: 320px) {
    .Header_header__3Cm3j {
        padding: 0.5rem 1rem;
    }
}
.Footer_footer__20Ijg {
    position: relative;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem 2.5rem;
    background-color: #292c6d;
}

.Footer_author__2GxaG,
.Footer_socials__3e_Xz {
    cursor: pointer;
}

.Footer_socials__3e_Xz {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    font-size: 2rem;
}

.Footer_socials__3e_Xz a,
.Footer_author__2GxaG {
    color: rgba(255, 255, 255, 0.678);
}

@media screen and (max-width: 320px) {
    .Footer_footer__20Ijg {
        flex-direction: column;
        grid-gap: 5px;
        gap: 5px;
    }
}
.LoadingSpinner_spinner__2WG4_ {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    width: 90vw;
    height: 93vh;
    overflow: hidden;
    /* border: 1px solid red; */
}

.LoadingSpinner_spinner__2WG4_:after {
    content: " ";
    display: block;
    width: 90px;
    height: 90px;
    border-radius: 50%;
    border: 14px solid #a20910;
    border-color: #a20910 transparent #a20910 transparent;
    animation: LoadingSpinner_spinner__2WG4_ 1s linear infinite;
}

@keyframes LoadingSpinner_spinner__2WG4_ {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.NotFound_notfound__2nMBt {
    width: 90vw;
    height: 95vh;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    color: rgb(218, 218, 218);
    text-shadow: 0.1em 0.1em 0 hsl(200 50% 30%);
    font-size: 3.5rem;
    border: 1px solid red;
}

@media screen and (max-width: 376px) {
    .NotFound_notfound__2nMBt {
        font-size: 2.8rem;
    }
}
.Movies_container__1b8EF {
    width: 90%;
    margin: 1rem auto;
    margin-top: 1rem;
    /* border: 1px solid red; */
}

.Movies_container__1b8EF:first-child {
    margin-top: 5.063rem;
}

.Movies_title__10DsI {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.5rem;
}

.Movies_title__10DsI h2 {
    color: #b1a21b;
}

.Movies_title__10DsI a {
    text-decoration: none;
}

.Movies_button__yXOSh {
    display: block;
    margin: 0 auto;
    padding: 0 auto;
    border: none;
    background-color: #a20910;
    color: rgba(255, 255, 255, 0.815);
    padding: 0.6rem 1.5rem;
    border-radius: 15px;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
}

.Movies_button__yXOSh:hover {
    transform: scale(1.05);
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
    cursor: pointer;
}

.Movies_movies-container__3Glpq {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    padding: 1.5rem 0;
    grid-gap: 2rem;
    border-top: 1px solid #1b3c5d;
}

.Movies_no-results__2QYH0 {
    color: #fff;
    padding: 2rem 0;
}

@media screen and (max-width: 768px) {
    .Movies_movies-container__3Glpq {
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 1.2rem;
    }
}

@media screen and (max-width: 415px) {
    .Movies_movies-container__3Glpq {
        grid-template-columns: repeat(2, 1fr);
    }
}
.Movie_movie__3Dyej img {
