    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
}

.Movie_movie__3Dyej span {
    color: rgba(255, 255, 255, 0.856);
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
